





















import {Vue, Component} from 'vue-property-decorator'

@Component
export default class Directives extends Vue {
    url = '#'

    onButtonClick() {
        this.$notify({
            title: 'button被点击',
            message: 'button被点击'
        });
    }

    onDivClick() {
        setTimeout(() => {
            this.$notify({
                title: 'Div被点击',
                message: 'Div被点击'
            });
        })
    }
}
