module.exports = "<!--\r\n缩写：\r\n1. v-bind缩写\r\n\r\n完整语法\r\n<a v-bind:href=\"url\">...</a>\r\n\r\n缩写\r\n<a :href=\"url\">...</a>\r\n\r\n\r\n2. v-on缩写\r\n完整语法\r\n<a v-on:click=\"doSomething\">...</a>\r\n\r\n缩写\r\n<a @click=\"doSomething\">...</a>\r\n-->\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1. v-bind缩写</h4>\r\n            <div>不缩写： <a v-bind:href=\"url\">v-bind不缩写</a></div>\r\n            <div>缩写： <a :href=\"url\">v-bind缩写</a></div>\r\n        </div>\r\n\r\n        <div class=\"case-item\">\r\n            <h4>2. v-on缩写</h4>\r\n            <el-button type=\"primary\" @click.stop=\"onButtonClick\">缩写</el-button>\r\n            <el-button type=\"primary\" v-on:click.stop=\"onButtonClick\">不缩写</el-button>\r\n        </div>\r\n\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class Directives extends Vue {\r\n        url = '#'\r\n\r\n        onButtonClick() {\r\n            this.$notify({\r\n                title: 'button被点击',\r\n                message: 'button被点击'\r\n            });\r\n        }\r\n    }\r\n</script>\r\n\r\n";